  font-size: 13px;
  font-weight: normal;
  line-height: 24px;
}

aside {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 200px;
  background-color: #031320;
  color: rgba(255, 255, 255, 0.85);
}

aside .sidebar-scroll {
  overflow-y: scroll;
  position: absolute;
  top: 109px;
  left: 0;
